* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: whitesmoke;
  color: gray;
}

.Booklist {
  width: 120vw;
  max-width: 1000px;
  margin: 5rem auto;
  background-color: whitesmoke;
  display: grid;
  gap: 2rem;
}

@media screen and (min-width: 769px) {
  .Booklist {
    grid-template-columns: repeat(3, 10fr);
    align-items: start;
  }
}

.Book {
  background-color: #fff;
  opacity: 80%;
  border-radius: 1rem;
  padding: 1rem 2rem;
}

.Book > h1 {
  margin-top: 0.5rem;
}
.Book > h4 {
  font-size: 0.76rem;
  color: violet;
}

.Book > p {
  margin-top: 0.76rem;
}
